.para-link {
  color: #0000ff;
  text-decoration: none;
}

.chatbot-container {
  position: fixed;
  right: 40px;
  bottom: 18%;
  /* box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px; */
  width: 25%;
  font-family: "Inter", sans-serif;
}

.chatbot-button {
  position: fixed;
  text-align: center;
  right: 40px;
  bottom: 6%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  height: 80px;
  width: 80px;
  border-radius: 100%;
  background-color: #d71313;
  color: #eeeded;
  border: none;
}

.react-chatbot-kit-chat-container {
  width: 100%;
}

.react-chatbot-kit-chat-inner-container {
  background-color: #f5f7f8;
  border-radius: 5%;
}

.react-chatbot-kit-chat-bot-message {
  margin-left: 3px;
}

.chatbot-header {
  display: flex;
  align-items: center;
  height: 15%;
  border-bottom: #dddddd solid 1px;
  font-weight: bold;
  font-size: larger;
}

.chatbot-bot-message {
  background-color: #eee;
  padding: 10px;
  padding-bottom: 15px;
  border-radius: 0px 70.4px 70.4px 30.4px;
  font-size: 0.9rem;
  color: #505050;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  position: relative;
  width: 184.5px;
  margin-left: 5px;
  text-align: left;
  white-space: pre-line;
}

.source-para {
  font-size: 0.5rem;
  color: #0000ff;
  font-style: normal;
  font-weight: 200;
  line-height: normal;
  position: relative;
  white-space: pre-line;
  text-align: left;
  margin: 0;
  margin-left: 10px;
}

.spacing-sources {
  width: 100%;
  height: 1px;
  padding: 1px;
}

.chatbot-user-message {
  background-color: var(--blue, #3369ff);
  padding: 10px;
  border-radius: 70.4px 0px 70.4px 70.4px;
  font-size: 0.9rem;
  color: #fff;
  position: relative;
  text-align: left;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.react-chatbot-kit-chat-message-container {
  background-color: #f5f7f8;
  height: 380px;
}
.react-chatbot-kit-chat-input {
  border-radius: 84.48px;
  background: #fff;
  box-shadow: 14.08px 11.264px 56.32px 0px rgba(0, 0, 0, 0.13);
}

.react-chatbot-kit-chat-btn-send {
  border-radius: 40px;
  box-shadow: 14.08px 11.264px 56.32px 0px rgba(0, 0, 0, 0.13);
}

.text-disable {
  cursor: not-allowed;
}

@media only screen and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
  .chatbot-container {
    width: 90%;
    right: 30px;
    bottom: 14%;
    height: 80%;
  }

  .chatbot-container > div {
    height: 100%;
  }

  .chatbot-container > div > div {
    height: 100%;
  }

  .react-chatbot-kit-chat-container {
    height: 100%;
  }

  .react-chatbot-kit-chat-inner-container {
    height: 100%;
  }

  .react-chatbot-kit-chat-message-container {
    height: 80%;
  }

  .chatbot-button {
    right: 30px;
    bottom: 2%;
    height: 100px;
    width: 100px;
  }

  .chatbot-user-message {
    /* width: 70%; */
    font-size: 1.1rem;
  }

  .chatbot-bot-message {
    width: 70%;
    font-size: 1.1rem;
  }
}

@media only screen and (max-width: 600px) {
  .chatbot-user-message {
    /* width: 70%; */
    font-size: 0.9rem;
  }

  .chatbot-bot-message {
    width: 70%;
    font-size: 0.9rem;
  }

  .chatbot-container {
    bottom: 11%;
    right: 10px;
  }

  .chatbot-button {
    right: 10px;
    height: 50px;
    width: 50px;
  }
}
